<template>
  <div class="offfice">


    <h5>   总计{{total}}条数据</h5>



    <el-form :inline="true" class="demo-form-inline" :loading="loading">
      <el-form-item>

      </el-form-item>

      <el-form-item label="姓名">
        <el-input v-model="search" placeholder="输入姓名查询" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" @click="fetch" size="small">查询</el-button>
          <el-button  @click="add_form.visible=true" size="small">增加考试</el-button>

        </el-button-group>
      </el-form-item>

    </el-form>


    <el-table
      :data="list"
      style="width: 100%"
      border
      size="mini"
      v-loading="loading"
    >

      <el-table-column prop="id" label="id"></el-table-column>
      <el-table-column prop="total" label="总考费"></el-table-column>
      <el-table-column prop="desc"  label="描述信息"></el-table-column>


      <el-table-column prop="time"  label="考试时间"></el-table-column>

      <el-table-column  label="操作"  >
        <template scope="scope">
          <el-button-group>
            <el-button type="danger" @click="remove(scope.row)" size="small">删除</el-button>
            <el-button @click="open_edit(scope.row)" size="small">编辑</el-button>
            <el-button @click="$router.push('/school/'+scope.row.id)" size="small">考点分配</el-button>
          </el-button-group>

        </template>
      </el-table-column>
    </el-table>


    <el-dialog title="增加考试信息" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">


        <el-form-item label="总监考费">
          <el-input v-model="add_form.total" placeholder="监考费"></el-input>
        </el-form-item>



        <el-form-item label="输入时间">
          <el-date-picker
            v-model="add_form.time"
            align="right"
            type="date"
            placeholder="选择日期"
            format="yyyy-MM-dd"
           >
          </el-date-picker>


        </el-form-item>

        <el-form-item label="描述">
          <el-input   type="textarea" v-model="add_form.desc" placeholder="输入描述"></el-input>
        </el-form-item>


      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="add" size="small">确 定</el-button>
        </span>
    </el-dialog>


    <el-dialog title="编辑老师信息" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline"  label-width="100px">


        <el-form-item label="总监考费">
          <el-input v-model="edit_form.total" placeholder="监考费"></el-input>
        </el-form-item>



        <el-form-item label="输入时间">
          <el-date-picker
            v-model="edit_form.time"
            align="right"
            type="date"
            placeholder="选择日期"
            format="yyyy-MM-dd"
          >
          </el-date-picker>


        </el-form-item>

        <el-form-item label="描述">
          <el-input   type="textarea" v-model="edit_form.desc" placeholder="输入描述"></el-input>
        </el-form-item>
      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>

    <el-dialog title="批量上传老师信息" :visible.sync="upload_dialog.visible"
               size="small"


    >
      <el-row>
        <el-button size="small">下载模板文件</el-button>
      </el-row>
      <br/>



      <el-upload
        class="upload-demo"
        drag
        style="width: 100%"
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>


      <span slot="footer" class="dialog-footer">
          <el-button @click="upload_dialog.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>



    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>


  import request from './config'
  import moment from 'moment'

  export default {

    name: 'app',
    data () {
      return {
        school_id:window.school_id,
        list:[],
        total:0,
        search:'',
        page_size:10,
        current_page:1,
        loading:false,
        add_form:{
          total:'',
          desc:'',
          time:'',
          visible:false
        },
        edit_form:{
          id:'',
          total:'',
          desc:'',
          time:'',
          visible:false
        },
        active_school_id:'-1',
        school_list:[],
        upload_dialog:{
          visible:false
        },
        user:window.user
      }
    },

    watch:{
      current_page(){
        this.fetch()
      }
    },

    methods:{
      change_page(p){
        console.log(p)
      },
      open_edit(data){
        console.log(data)
        data.visible = true
        this.edit_form = {
          visible:true,
          id:data.id,
          total:data.total,
          desc:data.desc,
          time:data.time,
        }
      },
      async fetch(){

        var _limit = this.page_size,
          _page = this.current_page,
          search = this.search,
          that = this

        this.loading = true
        console.log(window.school,'window.school')
        var response = await request.get('/api/exam/?office_id='+window.office.id)
        this.list = response.data.objects
        this.loading = false
      },
      async add(){
        var add_form = this.add_form
        add_form.office_id = window.office.id
        add_form.time = moment(add_form.time).format('YYYY-MM-DD')


        console.log(add_form,'add_form')

        var response = await request.post('/api/exam/',add_form)
        this.fetch()
        this.add_form.visible = false
        this.$message({
          type: 'success',
          message: '成功添加考试'
        });
      },
      async remove(data){
        try {
          await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          var response = await request.delete('/api/exam/'+data.id+'/')

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }catch (e){
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        }

      },
      async edit(){
        var edit_form = this.edit_form
        console.log(edit_form)
        var id = edit_form.id
        edit_form.time = moment(edit_form.time).format('YYYY-MM-DD')
        edit_form.office_id = window.office.id


        var response = await request.put('/api/exam/'+id +'/',edit_form)
        this.edit_form.visible = false
        this.$message({
          type: 'success',
          message: '修改信息成功!'
        });
        this.fetch()
      },

    },
    async mounted(){

      await this.fetch()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
  h1, h2 {
    font-weight: normal;
  }
  .teacher{
    box-sizing:border-box;
    padding:20px;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  .el-upload{
    display: block ;
  }
  .el-upload-dragger{
    width: 100% ;
  }

</style>



