import Vue from 'vue'
import Router from 'vue-router'
import user from '@/components/user'
import exam from '../exam.vue'
import school from '../school.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'exam',
      component: exam
    },
    {
      path: '/exam',
      name: 'exam',
      component: exam
    },
    {
      path: '/school/:id',
      name: 'school',
      component: school
    },
  ]
})



