<template>
  <div id="app">
    <div class="top-bar">
      <div>考试管理系统-学校端</div>
      <div></div>
      <span class="name">欢迎：{{user.name}}<a href="/logout/">退出</a></span>
    </div>
    <router-view/>


  </div>
</template>

<script>
  export default {
    name: 'app',
    data(){

      return {
        name,
        office:window.office,
        user:window.user,
      }
    },
    methods:{
      route(name){
        this.$router.push(name)
      }
    }
  }
</script>

<style>
  body,html{
    width: 100%;
    height: 100%;

  }
  div{
    margin: 0;
    padding: 0;
  }
  #app {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    min-width: 1280px;

  }
  .top-bar{
    background:#409EFF ;
    line-height: 70px;
    text-align: center;
    color: #ffffff;
    font-size: 30px;
    text-shadow: 10px 10px 4px 4px rgba(0,0,0,0.8);
    position: relative;
  }

  .name{
    position: absolute;
    right: 20px;
    line-height: 70px;
    top: 0;
    bottom: 0;
    font-size: 14px;
  }
</style>



