<template>
  <div class="offfice">


    <h3>
      {{exam_item.desc}}:总考费{{exam_item.total}}
    </h3>



    <el-form :inline="true" class="demo-form-inline" :loading="loading">
      <el-form-item>

      </el-form-item>

      <el-form-item label="姓名">
        <el-input v-model="search" placeholder="输入姓名查询" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" @click="fetch" size="small">查询</el-button>
          <el-button  @click="add_form.visible=true" size="small">增加新考点</el-button>

        </el-button-group>
      </el-form-item>

    </el-form>


    <el-table
      :data="list"
      style="width: 100%"
      border
      size="mini"
      v-loading="loading"
    >

      <el-table-column prop="id" label="id"></el-table-column>
      <el-table-column prop="school_name" label="学校"></el-table-column>
      <el-table-column label="这次考试的总监考费">
        <template scope="scope">
          {{exam_item.total}}
        </template>
      </el-table-column>
      <el-table-column prop="total"  label="该学校的总考费"></el-table-column>




      <el-table-column  label="操作"  >
        <template scope="scope">
          <el-button-group>
            <el-button type="danger" @click="remove(scope.row)" size="small">删除</el-button>
            <el-button @click="open_edit(scope.row)" size="small">编辑</el-button>

          </el-button-group>

        </template>
      </el-table-column>
    </el-table>


    <el-dialog title="增加老师信息" :visible.sync="add_form.visible"
    >
      <el-form class="demo-form-inline" label-width="100px">
        <el-form-item label="选择考点">
          <el-select v-model="add_form.school_id" placeholder="请选择">
            <el-option
              :disabled="s.disabled"
              v-for="s in school_list"
              :key="s.id"
              :label="s.name"
              :value="s.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="监考费">
          <el-input v-model="add_form.total"></el-input>
        </el-form-item>
      </el-form>



      <span slot="footer" class="dialog-footer">
          <el-button @click="add_form.visible=false" size="small">取 消</el-button>
          <el-button type="primary" @click="add" size="small">确 定</el-button>
        </span>
    </el-dialog>


    <el-dialog title="编辑学校" :visible.sync="edit_form.visible"
    >
      <el-form class="demo-form-inline"  label-width="100px">

        <el-form-item label="监考费">
          <el-input v-model="edit_form.total"></el-input>
        </el-form-item>

      </el-form>


      <span slot="footer" class="dialog-footer">
          <el-button @click="edit_form.visible=false">取 消</el-button>
          <el-button type="primary" @click="edit">确 定</el-button>
        </span>
    </el-dialog>









    <br>
    <el-pagination

      layout="prev, pager, next"
      :total="total"
      @current-change="(p)=>current_page=p"
      :current-page="current_page"
      :page-size="page_size"
    >
    </el-pagination>
  </div>
</template>

<script>


  import request from './config'
  import ElInput from "../node_modules/element-ui/packages/input/src/input.vue";


  export default {

    components: {ElInput},
    name: 'school',
    data () {
      return {
        office_id:window.office.id,
        list:[],
        total:0,
        search:'',
        page_size:10,
        current_page:1,
        loading:false,
        add_form:{
          school_id:'',
          total:'',
          visible:false
        },
        edit_form:{
          school_id:'',
          total:'',
          visible:false
        },

        user:window.user,
        id:-1,
        exam_item:{

        },
        school_list:[],

      }
    },

    watch:{
      current_page(){
        this.reload()
      }
    },

    methods:{
      change_page(p){
        console.log(p)
      },
      open_edit(data){
        console.log(data)
        data.visible = true
        this.edit_form = {
          visible:true,
          id:data.id,
          total:data.total
        }
      },
      async fetch(){

        var _limit = this.page_size,
          _page = this.current_page,
          search = this.search,
          that = this

        this.loading = true
        var id = this.id
        var response = await request.get('/api/schoolexam/?exam_id='+id+'/')
        this.list = response.data.objects
        this.loading = false
      },
      async add(){
        var add_form = this.add_form
        add_form.exam_id = this.exam_item.id

        var response = await request.post('/api/schoolexam/',add_form)
        this.$message({
          type: 'success',
          message: '分配考点成功!'
        });
        this.add_form.visible = false
        this.reload()

      },
      async remove(data){
        try {
          await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
          var response = await request.delete('/api/schoolexam/'+data.id+'/')

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.reload()
        }catch (e){
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        }

      },
      async edit(){
        var edit_form = this.edit_form
        console.log(edit_form)
        var id = edit_form.id



        var response = await request.put('/api/schoolexam/'+id +'/',edit_form)
        this.$message({
          type: 'success',
          message: '修改信息成功!'
        });
        this.edit_form.visible = false
        this.reload()
      },
      async fetch_exam(){
        var id = this.id
        var response = await request.get('/api/exam/'+id +'/')
        this.exam_item = response.data
      },
      async fetch_school(){
        var response = await request.get('/api/school/')
        var data = response.data.objects

        var list = this.list
        data = data.map(function (obj) {
            obj.disabled = false
            var exist = list.find(function (o) {
              return o.school_id == obj.id
            })
            if(exist){
              obj.disabled = true
            }
            return obj
        })
        this.school_list = data

      },
      async reload(){
        this.id = this.$route.params.id
        await this.fetch()
        await this.fetch_exam()
        await this.fetch_school()
      }

    },
    async mounted(){
      this.reload()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
  h1, h2 {
    font-weight: normal;
  }
  .teacher{
    box-sizing:border-box;
    padding:20px;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  .el-upload{
    display: block ;
  }
  .el-upload-dragger{
    width: 100% ;
  }

</style>



